import styles from "./Profileinfo.module.css";
import React from "react";
import {ProfileType} from "../../../redux/profileReducer";
import {Prealoder} from "../../common/Prealoder/Prealoder";
import {ProfileStatusWithHooks} from "./ProfileStatus/ProfileStatusWithHooks";
import userPhoto from "../../../assets/img/user.png";
import {ThunkType} from "../../../redux/reduxStore";

export type ProfileInfoPropsType = {
    profile: ProfileType | null
    status: string
    updateUserStatus: (status: string) => ThunkType
    savePhoto: (file: File) => ThunkType
    isOwner: boolean
}

interface Event<T = EventTarget> {
    target: T;
}

export const ProfileInfo: React.FC<ProfileInfoPropsType> = (
    {
        profile,
        status,
        updateUserStatus,
        savePhoto,
        isOwner,

    }
) => {

    if (!profile) {
        return <Prealoder/>
    }

    const onMainPhotoSelected = (e: Event<HTMLInputElement>) => {
        if (e.target.files && e.target.files.length) {
            savePhoto(e.target.files[0])
        }
    }

    return (
        <div className={styles.userBanner}>
            <div className={styles.avatar}>
                <div className={styles.photo}>
                    <img src={profile.photos.small || userPhoto} alt={''}/>
                </div>
                {isOwner && <input type={'file'} onChange={onMainPhotoSelected}/>}

            </div>
            <div className={styles.description}>
                <div className={styles.fullName}>{profile.fullName}</div>
                <ProfileStatusWithHooks status={status}
                                        updateUserStatus={updateUserStatus}
                />
                <div className={styles.lookingForAJob}>
                    <span>Looking for a Job: {profile.lookingForAJob ? "Yes" : "No"}</span>
                </div>
                {profile.lookingForAJob &&
                    <div className={styles.lookingForAJobDescription}>
                        <span>{profile.lookingForAJobDescription}</span>
                    </div>
                }
                <div className={styles.aboutMe}>
                    <span>About me: {profile.aboutMe}</span>
                </div>
                <div className={styles.contacts}>
                    {/*<span>Contacts: {Object.keys(profile.contacts).map(key => {*/}
                    {/*    return <ProfileContacts key={key} contactTitle={key} contactValue={profile.contacts[key]}/>*/}
                    {/*})}</span>*/}
                </div>
            </div>
        </div>
    )
}


export type ProfileContactsType = {
    contactTitle: string,
    contactValue: string
}

export const ProfileContacts: React.FC<ProfileContactsType> = ({contactTitle, contactValue}) => {
    return (
        <div>
            {contactTitle} : P{contactValue}
        </div>
    )
}